* {
  margin: 0;
  padding: 0;
  font-family: Space Mono, monospace;
  font-size: 24px;
}

@media (prefers-color-scheme: dark) {
  body {
    color: #ececec;
    background-color: #101010;
  }
}

@media (prefers-color-scheme: light) {
  body {
    color: #101010;
    background-color: #ececec;
  }
}

body nav {
  margin: 2rem;
  display: flex;
}

body nav .spacer {
  flex-grow: 1;
}

body nav ul li {
  text-align: right;
  list-style: none;
}

body .cat {
  opacity: .5;
  position: absolute;
  bottom: 2rem;
  left: 2rem;
}

@media (prefers-color-scheme: light) {
  a {
    color: #00008f;
  }
}

@media (prefers-color-scheme: dark) {
  a {
    color: #8a8aff;
  }
}

.tooltip:hover {
  position: relative;
}

.tooltip:hover:after {
  content: attr(data-tooltip);
  white-space: nowrap;
  z-index: 20;
  border-radius: 5px;
  padding: 4px 8px;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}

@media (prefers-color-scheme: light) {
  .tooltip:hover:after {
    color: #101010;
    background-color: #ececec;
    border: 1px solid #101010;
  }
}

@media (prefers-color-scheme: dark) {
  .tooltip:hover:after {
    color: #ececec;
    background-color: #101010;
    border: 1px solid #ececec;
  }
}
